import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {HomeComponent} from './home/home.component';
import { AboutComponent } from './about/about.component';
import { ContactComponent } from './contact/contact.component';
import { FormsComponent } from './forms/forms.component';
import {EventsComponent} from './events/events.component';
import {FoodComponent} from './food/food.component';
import {GalleryComponent} from './gallery/gallery.component';
import{RatingsComponent} from './ratings/ratings.component';
import {LoginComponent} from './adminpanel/login/login.component';
import {DashboardComponent} from './adminpanel/dashboard/dashboard.component';
import {LayoutComponent} from './layout/layout.component';
import { dashCaseToCamelCase } from '@angular/compiler/src/util';
import { AuthGuard } from './adminpanel/Gaurds/auth.guard';




const routes: Routes = [{path:'',component:LayoutComponent,children :[
  {path:'',component:HomeComponent},
{path:'home',component:HomeComponent},

{path:'about',component:AboutComponent},
{path:'contact',component:ContactComponent},
{path:'form',component:FormsComponent},
{path:'events',component:EventsComponent},
{path:'food',component:FoodComponent},
{path:'gallery',component:GalleryComponent},
{path:'ratings',component:RatingsComponent},
]},
{path:'login',component:LoginComponent},
{path:'dashboard',component:DashboardComponent,canActivate:[AuthGuard]},

]

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
