import { Component, OnInit } from '@angular/core';
import {AngularFirestore} from '@angular/fire/firestore'
import { Customer } from '../model/customer';
import { from } from 'rxjs';
import { NgForm } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
 
@Component({
  selector: 'app-forms',
  templateUrl: './forms.component.html',
  styleUrls: ['./forms.component.css'],
  providers:[AngularFirestore]
})
export class FormsComponent implements OnInit {
guest:Customer;
emailurl="https://fancynamehotel.000webhostapp.com/fancynamehotel/SendEmail.php"

  constructor(private firestore: AngularFirestore, private http:HttpClient) { }

  ngOnInit() {

    

    this.resetForm();
  }
  
  submit(frm:NgForm){
    this.http.post(this.emailurl, JSON.stringify(frm.value)).subscribe((result)=>{
      alert("email Sent")
    })
this.firestore.collection("/bookings").add(frm.value).then((res)=>{
  console.log(res)
})  }

  resetForm(frm?:NgForm){
    if(frm!=null){frm.resetForm()}
    this.guest={
      name:'',
      email:'',
      phone:null,
      age:null,
      rooms:null,
      type:'',
      guests:null,
      checkin:null,
      checkout:null,

    }
    

  }
  delete(id){
    event.preventDefault()
    this.firestore.doc("/bookings/"+id).delete().then((res)=>{
      console.log(res)
    }).catch((err)=>{
      console.log(err)
    })


  }
 
}
