import { Component, OnInit } from '@angular/core';
import {AngularFirestore } from '@angular/fire/firestore';
import { Customer } from 'src/app/model/customer';
import{ DatePipe} from '@angular/common'
import { Router } from '@angular/router';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
  providers:[{
    provide:AngularFirestore,useClass:AngularFirestore},
  {
    provide:DatePipe,
  }]
})
export class DashboardComponent implements OnInit {

  bookings; 
  guest:Customer;
  constructor(private firestore:AngularFirestore,private _router:Router) {

   }
  

  ngOnInit() {
    this.firestore.collection("/bookings").snapshotChanges().subscribe((res)=>{
      this.bookings=res.map(item=>{
        return{
          id:item.payload.doc.id,
          ...item.payload.doc.data()
        };
      })
    })
  }
  delete(id){
    event.preventDefault()
    this.firestore.doc("/bookings/"+id).delete().then((res)=>{
      console.log(res)
    }).catch((err)=>{
      console.log(err)
    })


  }
 logout(){
   this._router.navigate(["/login"])
 }

 }
